import React from 'react';

//Image import
import home1 from "../img/home1.png";
//Styled component
import { Container, Description, Image, Hide } from '../styles';
//Framer motion
import { motion } from 'framer-motion';
import { titleAnim, fade, photoAnim } from '../animation';
//component import
import Wave from './Wave';
import styled from 'styled-components';

const AboutSection = () => {

    return(
        <Container>
            <Description>
                <motion.div>
                    <Hide>
                        <motion.h2 variants={titleAnim}>We work to make</motion.h2>
                    </Hide>
                    <Hide>
                        <motion.h2 variants={titleAnim}>your <span>dreams</span> </motion.h2>
                    </Hide>
                    <Hide>
                        <motion.h2 variants={titleAnim}>come true.</motion.h2>
                    </Hide>
                </motion.div>
                <motion.p variants={fade}>
                    Contact us for any photography or videography ideas that you have. We have professional with amazing skills
                </motion.p>
                <motion.button variants={fade}>Contact Us</motion.button>
            </Description>
            <Image>
                <motion.img variants={photoAnim} src={home1} alt="person profile"/>
            </Image>
            <Wave />
        </Container>
    );
};

export default AboutSection;